/*Передаем property= параметр условия, Breakpoint= значение условия*/
/* elements */
.burger {
  position: relative;
  border: none;
  cursor: pointer;
  background: transparent;
  z-index: 5;
  min-width: 50px;
  display: flex;
  flex-flow: column;
  row-gap: 6px;
  align-items: center;
  padding: 0;
  user-select: none;
}
.burger:hover .burger__line {
  background-color: #D22856;
}
.burger:active .burger__line {
  opacity: 0.3;
}
.burger__line {
  width: 100%;
  height: 4px;
  background-color: #FFFFFF;
  box-sizing: border-box;
  border-radius: 1px;
  transition: 0.2s;
  user-select: none;
}

.backgr:before, .backgr:after {
  background: #F2F2F2;
  content: "";
  display: block;
  height: 50%;
  left: 0;
  position: absolute;
  right: 0;
  z-index: -1;
  -webkit-backface-visibility: hidden;
}
.backgr:before {
  top: 0px;
  transform: skewY(-6deg);
  transform-origin: 100% 0;
}
.backgr:after {
  bottom: 0;
}
.backgr--deg3:before {
  transform: skewY(-3deg);
}

/* blocks */
