@keyframes ani {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.main-nav {
  width: 100%;
  display: none;
  opacity: 0;
  animation: ani 0.5s forwards;
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.main-nav__item {
  height: 63px;
  border-bottom: solid 1px #000000;
  border-top: solid 1px rgba(255, 255, 255, 0.15);
}
.main-nav__link {
  box-sizing: border-box;
  display: block;
  text-decoration: none;
  color: inherit;
  width: 100%;
  padding: 18px 0;
  text-transform: uppercase;
  transition: 0.1s;
}
.main-nav__link:hover {
  color: #D22856;
}
.main-nav__link:active {
  opacity: 0.3;
}
@media only screen and (min-width: 1200px) {
