    z-index: 3;
    min-height: 100%;
    flex-wrap: wrap;
  }
  .main-nav__burger {
    display: none;
  }
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    margin-right: 0;
    column-gap: 78px;
  }
