    border: none;
  }
}

.slider {
  position: relative;
  touch-action: pan-y;
}
.slider__item {
  width: 100%;
  transition: 0.5s ease-in;
  opacity: 0;
  position: absolute;
  z-index: -1;
  display: block;
  left: 0;
  top: 0;
  transform: translateX(-50%);
}
.slider__item--active {
  opacity: 1;
  position: static;
  transform: translateX(0);
  transition: 0.3s ease-in;
  z-index: 1;
}
.slider__toggles {
  display: flex;
  justify-content: center;
  column-gap: 15px;
  align-items: center;
  height: 12px;
}
.slider__toggle {
  padding: 0;
  margin: 0;
  border: none;
  position: relative;
  background: none;
  cursor: pointer;
  width: 15px;
  height: 15px;
}
.slider__toggle::before {
  content: "";
  display: block;
  width: 6px;
  height: 6px;
  background-color: #283645;
  opacity: 0.3;
  border-radius: 6px;
  transition: 0.1s;
  scale: 0.9;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.slider__toggle:hover {
  opacity: 0.6;
}
.slider__toggle:active {
  opacity: 0.1;
}
.slider__toggle--current::before {
  box-sizing: content-box;
  background-color: #FFFFFF;
  border: 3px solid #283645;
  opacity: 1;
}
.slider__toggle--current::before:hover {
  opacity: 1;
}
.slider__toggle--current::before:active {
  transition: 0.2s;
  scale: 0.75;
}
.slider__btn {
  width: 60px;
  height: 60px;
  z-index: 5;
  border: none;
  cursor: pointer;
  background: url("./images/icon-arrow.svg") center right no-repeat;
  margin: 0;
  padding: 0;
  opacity: 0.3;
  transition: all 0.2s ease-in-out;
}
.slider__btn--right {
  rotate: 180deg;
}
.slider__btn--right:hover {
  transform: translate(-50%, 50%) scale(1.5);
}
.slider__btn--left:hover {
  transform: translate(-50%, -50%) scale(1.5);
}
.slider__btn:hover {
  opacity: 0.6;
}
.slider__btn:active {
  opacity: 0.1;
}

