@keyframes popup {
  0% {
    transform: translateY(-100%);
    opacity: 0;
  }
  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}
@keyframes popupdesk {
  0% {
    transform: translateY(-100%) translateX(-50%);
    opacity: 0;
  }
  100% {
    transform: translateY(0%) translateX(-50%);
    opacity: 1;
  }
}
.page-header {
  width: 100vw;
  position: fixed;
  display: flex;
  flex-wrap: wrap;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 2;
  left: 0;
  top: 0;
  box-sizing: border-box;
  overflow: hidden;
  animation: popup 0.4s;
  animation-timing-function: ease-in-out;
}
.page-header__plank {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
}
.page-header--opened {
  background-color: #283645;
}
.page-header__plank {
  height: 69px;
}
.page-header--opened .page-header__plank {
  border-bottom: solid 1px #000000;
}
.page-header--opened .main-nav {
  display: block;
}
.page-header--opened .burger {
  min-width: initial;
  width: 23px;
  height: 23px;
  display: block;
  margin-right: 12px;
}
.page-header--opened .burger__line {
  border-radius: 0;
  transition: 0.2s;
}
.page-header--opened .burger__line:nth-child(2) {
  opacity: 0;
}
.page-header--opened .burger__line:nth-child(1) {
  transform: translate(0, 200%) rotate(45deg);
  -webkit-transform: translate(0, 200%) rotate(45deg);
  -ms-transform: translate(0, 200%) rotate(45deg);
}
.page-header--opened .burger__line:nth-child(3) {
  transform: translate(0, 0) rotate(-45deg);
  -webkit-transform: translate(0, 0) rotate(-45deg);
  -ms-transform: translate(0, 0) rotate(-45deg);
}
@media only screen and (min-width: 660px) {
  .page-header__plank {
    height: 110px;
  }
}
@media only screen and (min-width: 1200px) {
  .page-header {
    position: absolute;
    flex-wrap: nowrap;
    transform: translateX(-50%);
    left: 50%;
    max-width: 1200px;
    margin-bottom: -110px;
    padding-right: 130px;
    animation: popupdesk 0.4s;
  }
  .page-header__plank {
    width: min-content;
  }
}

