.page-footer__copyright:hover .page-footer__img {
  fill: #283645;
  transition: 0.2s;
}
.page-footer__copyright:active .page-footer__img {
  opacity: 0.3;
}
.page-footer__svg {
  margin-left: 13px;
}
@media only screen and (min-width: 660px) {
  .page-footer {
    flex-flow: row;
    justify-content: space-between;
  }
}
@media only screen and (min-width: 1200px) {
  .page-footer {
    padding-top: 70px;
    padding-bottom: 70px;
  }
}

.tariff {
  overflow: hidden;
  position: relative;
}
.tariff__wrap {
  padding-top: 100px;
  padding-bottom: 57px;
  overflow: hidden;
}
.tariff__table {
  border-collapse: collapse;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  transition: 0.5s ease-in-out;
  margin-bottom: 42px;
}
.tariff__toggles {
  position: absolute;
  left: 50%;
  transform: translate(-50%);
}
.tariff__text {
  text-transform: uppercase;
}
.tariff__column-head {
  box-sizing: border-box;
  background-color: #283645;
  border-left: 3px solid #E5E5E5;
  text-align: center;
  height: 94px;
}
.tariff__column-head .tariff__text:nth-child(1) {
  margin-top: 0;
  margin-bottom: 10px;
}
.tariff__column-head .tariff__text:nth-child(2) {
  margin: 0;
}
.tariff__column-head:nth-child(2) {
  border-left: 3px solid #283645;
}
.tariff__column-head:last-child {
  border-right: 3px solid #283645;
}
.tariff__column-head--empty {
  background-color: inherit;
  display: none;
}
.tariff__column-head--special {
  position: relative;
  overflow: hidden;
}
.tariff__column-head--special::before {
  content: "ХИТ";
  display: block;
  color: #FFFFFF;
  box-sizing: border-box;
  line-height: 20px;
  font-size: 14px;
  position: absolute;
  left: -30px;
  transform: translateX(-2%);
  width: 120px;
  height: 23px;
  background-color: #D22856;
  rotate: -45deg;
}
.tariff__line .tariff__value:nth-child(n+2) {
  border-top: 3px solid #283645;
}
.tariff__line-head {
  display: none;
}
.tariff__value {
  box-sizing: border-box;
  min-width: 87.5vw;
  height: 59px;
  background-color: #FFFFFF;
  border: 3px solid #E5E5E5;
  background-repeat: no-repeat;
  background-position: 94% center;
}
.tariff__value--yes {
  background-image: url("./images/yes.svg");
}
.tariff__value--no {
  background-image: url("./images/no.svg");
}
.tariff__prefix {
  text-transform: uppercase;
  padding-left: 20px;
}
@media only screen and (min-width: 660px) {
  .tariff__value {
    min-width: 153px;
    height: 45px;
    background-position: center center;
  }
  .tariff__prefix {
    display: none;
  }
  .tariff__toggles {
    display: none;
  }
  .tariff__line-head {
    display: table-cell;
    padding-left: 15px;
  }
  .tariff__column-head {
    height: 75px;
  }
  .tariff__column-head--empty {
    display: initial;
    border: none;
  }
  .tariff__column-head--special::before {
    left: -42px;
    height: 19px;
    line-height: 16px;
  }
}
@media only screen and (min-width: 1200px) {
  .tariff__wrap {
    padding-top: 159px;
  }
  .tariff__table {
    margin-bottom: 0;
  }
  .tariff__column-head {
    height: 94px;
    padding: 0;
  }
  .tariff__value {
    min-width: 233px;
    height: 59px;
  }
  .tariff__line-head {
    font-size: 18px;
    padding-left: 22px;
  }
  .tariff__column-head .tariff__text:nth-child(1) {
    font-size: 18px;
    margin-bottom: 2px;
  }
  .tariff__column-head .tariff__text:nth-child(2) {
    font-size: 24px;
  }
  .tariff__column-head--special::before {
    left: -35px;
  }
}

.map__frame {
  display: block;
  clip-path: polygon(0% 0%, 50% 15%, 100% 0%, 100% 100%, 0% 100%);
}
@media only screen and (min-width: 660px) {
  .map__frame {
    height: 374px;
  }
}
@media only screen and (min-width: 1200px) {
  .map__frame {
    height: 502px;
  }
}