.page {
    
    height: 100%;
    &__body {
        min-height: 100%;
        max-width: 1200px;
        padding: 0;
        margin: 0;
        box-sizing: border-box;
        overflow: hidden scroll;
        margin: 0 auto;
        box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2);
        display: grid;
        grid-template-rows: 1fr min-content;
    };

    @include breakpoint (min-width, $desktop-width-Breakpoint) {
        
    }
};